/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */

/* Document */
html {
	box-sizing: border-box; //Makes the box width as the actual rendered width independent of the margin and padding.
	line-height: 1.15; //Normalize the Line Height across all browsers.
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%; //Prevent adjustments of font size after orientation changes in iOS.
}

/* stylelint-disable */
*,
*:before,
*:after {
	
	box-sizing: inherit; /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */

	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
/* stylelint-enable */

/**
 * 1. Fallback for when there is no custom background color defined. [WordPress]
 * 2. Remove default margin. [normalize.css]
 * 3. Set the base elements `font-size` to the value of your choosing.
 * 4. Work out the unitless `line-height` for your project based around your
 *    desired `line-height` (defined previously in pixels), and your projects
 *    base font size.
 */

/* Sections */

body {
	margin: 0; //Remove Margin in all browsers
	@include font-family();
	@include body-text( body );

	background: $color__background-body;
	color: $color__body-text;

	// WP Editor
	&.mce-content-body {
		font-family: inherit;
		margin: 15px;
	}
}

main {
	display: block; //Render the `main` element consistently in IE.
}

hr {
	box-sizing: content-box; // Add the correct box sizing in Firefox.
	height: 0; // Add the correct box sizing in Firefox.
	overflow: visible; // Show the overflow in Edge and IE.
}

// Gutenberg block quote support.
blockquote,
q,
blockquote.wp-block-quote {
	quotes: "" "";
	border-left: 4px solid $gray-150;
	font-style: italic;
	margin: 0 0 $font__base-line-height $font__base-line-height;
	padding-left: 16px;
	background: transparent;
	text-align: left;

	p {
		font-size: 16px;
		font-style: normal;
		font-weight: 400;
	}

	/* stylelint-disable */
	cite,
	.wp-block-quote__citation {
		display: block;
		font-size: 16px;
		font-weight: 700;
		margin-top: 12px;
		text-transform: uppercase;
	}
	/* stylelint-enable */

	p:last-of-type {
		margin-bottom: 0;
	}

	&.is-large,
	&.is-style-large {
		margin: 0 auto;

	}

}

/* Pull Quote
--------------------------------------------- */

.wp-block-pullquote {
	border: none;
	padding: 0;

	blockquote {
		border-left: 0;
		border-top: 8px solid $gray-150;
		border-bottom: 8px solid $gray-150;
		padding: 16px 0;
		text-align: center;
		max-width: 50%;
		margin: 0 auto;
	}

	&.alignleft,
	&.alignright {

		blockquote {
			max-width: 100%;
		}
	}
}

img {
	border-style: none; //Remove the border on images inside links in IE 10.
	height: auto;
	width: 100%;
	vertical-align: middle;
}

/* Image Support
--------------------------------------------- */
.wp-block-image {
	&.size-medium,
	&.size-thumbnail,
	&.is-resized {
		img {
			width: auto;
		}
	}

	figcaption {
		text-align: left;
		padding: 10px 0;
	}
}

figure {
	max-width: 100%;
}

sub,
sup {
	position: relative;
	font-size: 75%;
	line-height: 0;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

/*------------------------------------*\
    #HEADINGS
\*------------------------------------*/
/* stylelint-disable */
h1 {
	@include body-text( h1 )
}

h2 {
	@include body-text( h2 )
}

h3 {
	@include body-text( h3 )
}

h4 {
	@include body-text( h4 )
}

h5 {
	@include body-text( h5 )
}

h6 {
	@include body-text( h6 )
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 10px 0;
	text-rendering: optimizelegibility;
}

/*------------------------------------*\
    #TABLE
\*------------------------------------*/

table {
	max-width: 100%;
	background-color: transparent;
	border-collapse: collapse;
	border-spacing: 0;

	@include mq( medium ) {
		max-width: 350px;
	}
}

/* Button
--------------------------------------------- */

.wp-block-button {

	.wp-block-button__link {
		border-radius: 0;
		font-weight: 700;
		font-size: 16px;
		line-height: 18px;
		padding: 20px 24px;

		&:hover {
			background: $gray-200;
			text-decoration: none;
		}

	}
}
/* stylelint-enable */

/*------------------------------------*\
    Form Fields
\*------------------------------------*/

input,
button,
select,
textarea {
	font-family: $font__main;
}

select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
	display: inline-block;
	height: 20px;
	width: 206px;
	padding: 14px 6px; /* adjusting border-box difference on live site */
	margin: 0 0 10px;
	line-height: 1.43;
	color: $gray-300;
	vertical-align: middle;
	border-radius: 4px;
	background-color: $white;
	border: 1px solid $gray-200;
	box-shadow: inset 0 1px 1px $opacity-black-075;
	transition: border linear 0.2s, box-shadow linear 0.2s;
}

textarea {
	height: auto;
	padding: 4px 6px;
	max-width: 100%;
	outline: none;
	overflow: auto; // Remove the default vertical scrollbar in IE 10+.
}

select {
	box-shadow: none;
	transition: none;
	width: 220px;
	padding: 0 6px;
}

select,
input[type="file"] {
	height: 30px;
	line-height: 2.15;
}

button {
	margin: 0;
}

label {
	display: block;
	margin-bottom: 5px;
}

label,
input,
button,
select,
textarea {
	@include body-text( body );
	font-weight: 400;
}

label,
select,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
input[type="radio"],
input[type="checkbox"] {
	cursor: pointer;
}

input[type="search"] {
	-webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button {
	-webkit-appearance: none;
}

/*------------------------------------*\
    #OTHER
\*------------------------------------*/

/**
* 1. Add the correct font weight in Chrome, Edge, and Safari.
*/

b, //Bold
strong { //Strong
	font-weight: bolder; /* 1 */
}

pre { //Preformated Text
	display: block;
	padding: 9.5px;
	margin: 0 0 10px;
	line-height: 1.54;
	word-break: break-all;
	word-wrap: break-word;
	white-space: pre-wrap;
	background-color: $gray-160;
	border: 1px solid $opacity-black-015;
	border-radius: 4px;
	font-family: inherit; //Correct the inheritance and scaling of font size in all browsers. (Monospace Font preferably)
	font-size: 14px; // Correct the font sizing in all browsers
}

/**
* 1. Remove the bottom border in Chrome 57-
* 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
*/
  
abbr[title] { //Abbrevation
	border-bottom: none; /* 1 */
	text-decoration: underline; /* 2 */
	text-decoration: underline dotted; /* 2 */
}

code { //Code
	padding: 2px;
	color: #d14;
	white-space: nowrap;
	background-color: #f7f7f9;
	border: 1px solid #e1e1e8;
	font-family: inherit;
	font-size: 14px;
	border-radius: 3px;
}

/**
* 1. Correct the inheritance and scaling of font size in all browsers.
* 2. Correct the odd `em` font sizing in all browsers.
*/
kbd{ //Keyboard Input
	font-family: inherit; /* 1 */
	font-size: 14px; /* 2 */
	border-radius: 2px;
  	padding: 2px;
  	border: 1px solid black;
	
}

/**
* 1. Correct the inheritance and scaling of font size in all browsers.
* 2. Correct the odd `em` font sizing in all browsers.
*/
samp { //Sample Output
	font-family: inherit; /* 1 */
	font-size: 14px; /* 2 */
}